<template>
	<section>
		<!--  -->
		<section class="goods">
			<!--  -->
			<section class="kinds_wrapper">		<!--  -->
				<ul class="kinds" ref="kinds">
					<li v-for="(good,index) in goods" :class="{current:currentIndex===index}" @click="selectkind(index)"><div>{{good.name}}</div></li>
				</ul>
			</section>
			<!--  -->
			<section class="list" ref="list">	<!--  -->
				<section>
					<dl v-for="good in goods">
						<dt>{{good.name}}</dt>
						<dd v-for="food in good.foods" @click="selectDetail(food)">
							<section class="food">
								<img :src="food.icon" alt="">
								<section class="content">
									<h3>{{food.name}}</h3>
									<p>{{food.description}}</p>
									<p>{{food.sellCount}}&nbsp;&nbsp;&nbsp;{{food.rating}}%</p>
									<span><em>{{food.price}}</em></span>
									<del v-if="food.oldPrice">{{food.oldPrice}}</del>
								</section>
								<ele-count :food="food"></ele-count>
							</section>
						</dd>
					</dl>
				</section>
			</section>
		</section>
		<!--  -->
		<transition name="silder">
			<ele-detail v-show="detail_show" class="detail"></ele-detail>
		</transition>
		<!--  -->
		<ele-cart></ele-cart>
	</section>
</template>

<script>
	import {mapState,mapMutations} from 'vuex';
	//
	const IScroll = require('../../../static/js/iscroll-probe.js');
	
	//
	import Detail from '../Detail/Detail.vue'
	import Cart from '../Cart/Cart.vue'
	import Count from '../Count/Count.vue'
	
	export default {
		//
		data(){
			return {
				//
				currentIndex : 0,
				//dl
				listTop : []
			};
		},
		
		//
		mounted(){
			//
			this.kindsScroll = new IScroll( '.kinds_wrapper',{click:true} );
			
			//
			this.listScroll = new IScroll('.list',{probeType:2,click:true});
			
			//dom
			this.lis = Array.from( this.$refs.kinds.querySelectorAll('li') );
			//dom
			this.dls = Array.from( this.$refs.list.querySelectorAll('dl') );

			//dl
			this.dls.forEach( (item) => {
				//console.log(item);
				this.listTop.push( item.offsetTop );
			} );
			//console.log(this.listTop);
			
			let _this = this;
			//scroll
			this.listScroll.on( 'scroll', function(){
				//console.log( Math.abs(this.y) );
				//
				let dis = Math.abs(this.y);
				
				//dltopdl
				for( let i = 0; i < _this.listTop.length; i++ ){
					let start = _this.listTop[i];;
					let end = _this.listTop[i+1];

					//dltopdltopdlendundefinedl
					if( ( start <= dis && dis < end ) || !end ){
						_this.currentIndex = i;
						break;
					}
				}
				//console.log( _this.currentIndex );
				//
				_this.kindsScroll.scrollToElement( _this.lis[_this.currentIndex] );
			} );
		},
		
		//
		methods : {
			//
			selectkind(index){
				//
				this.currentIndex = index;
				
				//
				this.listScroll.scrollToElement( this.dls[index] );
			},
			
			//storemutations
			...mapMutations( [
				'show_control',
				'selectDetail'
			] ),
			
			//
			selectDetail(food){
				this.$store.commit( 'show_control', 'detail_show' );
				this.$store.commit( 'selectDetail', food );
			}
		},
		
		//
		computed : {
			//store
			...mapState( [
				'goods',
				'detail_show'
			] )
		},
		
		//
		components : {
			'ele-detail' : Detail,
			'ele-cart' : Cart,
			'ele-count' : Count
		}
	}
</script>

<style lang="less" scoped>
	/*  */
	.silder-enter,.silder-leave-active{
		transform:translateX(100%);
		opacity:0;
	}
	.silder-enter-active,.silder-leave-active{
		transition:all 0.5s;
	}
	/*  */
	.goods{
		background-color:#f3f5f7;
		display:flex;
		font-size:12px;
		color:#07111b;
		position:fixed;
		left:0;
		right:0;
		top:175px;
		bottom:50px;
		overflow-y:hidden;
		/*  */
		ul.kinds{
			min-width:80px;
			li{
				height:54px;
				padding:0 12px;
				&.current{
					background-color:#fff;
					div{border:none;}
				}
				div{
					height:100%;
					border-bottom:1px solid rgba(7,17,27,0.1);
					line-height:15px;
					display:flex;
					align-items:center;
				}
			}
		}
		/*  */
		section.list{
			color:rgb(147,153,159);
			width:100%;
			dl{
				dt{
					height:26px;
					line-height:26px;
					border-left:2px solid #d9dde1;
					padding-left:14px;
				}
				dd{
					background-color:#fff;
					&:not(:last-of-type) .food{
						border-bottom:1px solid #eaecef;
					}
					.food{
						margin:0 18px;
						padding:18px 0;
						display:flex;
						img{
							width:64px;
							height:64px;
							margin-right:10px;
						}
						.content{
							h3{
								font-size:14px;
								line-height:14px;
								color:rgb(7,17,27);
								font-weight:bold;
							}
							p{
								margin:8px 0;
							}
							span{
								color:#f01414;
								line-height:20px;
								margin-right:8px;
								em{
									font-size:16px;
									font-weight:bold;
								}
							}
							del{
								line-height:20px;
								font-weight:bold;
								font-size:12px;
							}
						}
					}
				}
			}
		}
	}
</style>


